<style scoped>
  @import url('../src/assets/css/to_do_list');
</style>

<template>
  <div id="app">
    <!-- TodoHeader -->
    <TodoHeader></TodoHeader>
    <!-- TodoInput -->
    <TodoInput v-on:addTodo="addTodo"></TodoInput>
    <!-- TodoList -->
    <TodoList v-bind:todoItems="todoItems" v-on:doneToggle="doneToggle" v-on:removeTodo="removeTodo"></TodoList>
    <!-- TodoFooter -->
    <TodoFooter v-on:clearAll="clearAll"></TodoFooter>
  </div>
</template>

<script>
import TodoHeader from '../src/components/todo/TodoHeader.vue';
import TodoInput from '../src/components/todo/TodoInput.vue';
import TodoList from '../src/components/todo/TodoList.vue';
import TodoFooter from '../src/components/todo/TodoFooter.vue';

export default {
  components : {
    TodoHeader,
    TodoInput,
    TodoList,
    TodoFooter
  },
  data : function(){
    return {
      todoItems : [
        { id: 1, todo: '🦷 치과방문하기', done: false },
        { id: 2, todo: '🤓 무한도전 재방송보기', done: false },
        { id: 3, todo: '📝 ES6 학습', done: false },
        { id: 4, todo: '🐶 강아지산책', done: false }
      ]
    };
  },
  methods : {
    addTodo(newTodoItem) {
      const maxid = this.$data.todoItems.map((item) => {return item.id}).reduce((pvalue, cvalue) => {return pvalue > cvalue ? pvalue : cvalue})
      const newid = maxid + 1;
      const todoItem = {
        id : newid,
        todo: newTodoItem,
        done: false
      };
      this.$set(this.$data.todoItems, this.$data.todoItems.length, todoItem);
    },

    doneToggle(id){
      const newTodoItems = this.$data.todoItems.map((item) => {
        if (item.id === id) {
          item.done = !item.done
        }
        return item;
      });
      this.$data.todoItems = newTodoItems;
    },

    removeTodo(id) {
      const newTodoItems = this.$data.todoItems.filter((item) => {
        if (item.id === id) {
          return false
        }
        return true;
      });
      this.$data.todoItems = newTodoItems;
    },

    clearAll() {this.$data.todoItems = [];}
  }
}
</script>