<template>
    <div class="add_list">
        <input type="text" placeholder="할 일을 추가해 주세요" v-model.trim:value="newTodoItem" v-on:keyup.enter="addTodo">
        <input type="submit" v-on:click="addTodo">
    </div>
</template>

<script>
export default {
    data : function(){
        return {
            newTodoItem : null
        }
    },
    methods: {
        addTodo(){
            this.$emit('addTodo', this.$data.newTodoItem);
            this.$data.newTodoItem = null;
        }
    }
}
</script>