<template>
    <ul class="task_list">
        <li
        v-for="todoItem in todoItems"
        v-bind:key="todoItem.id"
        v-bind:class="checked(todoItem.done)"
        >
            <input type="checkbox" id="task"/>
            <label for="task" v-bind:class="checked(todoItem.done)" v-on:click.stop="doneToggle(todoItem.id)" ><i></i></label>
            <span v-on:click.stop="doneToggle(todoItem.id)">{{todoItem.todo}}</span>
            <button class="delete-btn" v-on:click.stop="removeTodo(todoItem.id)"></button>
        </li>
    </ul>
</template>

<script>
export default {
    props: ["todoItems"],
    methods : {
        checked(done) {
            return done ? 'checked' : null;
        },
        doneToggle(id) {
            this.$emit('doneToggle', id);
        },
        removeTodo(id) {
            this.$emit('removeTodo', id);
        }
    }
}
</script>